@import url(https://fonts.googleapis.com/css?family=Poppins);
#graph {
  width: 100%;
  height: 100%;
}
#graph * {
  transition: opacity var(--fast);
}

.path_hitbox:hover {
  cursor: pointer;
  opacity: 1;
}

.popover {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: wrap;
  position: fixed;
  padding: 10px;
  outline: solid 2px var(--dark-gray);
  background: var(--black);
  color: var(--white);
  z-index: 99;
}
.popover_overlay {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 98;
}

.tooltip {
  font-size: 0.75em;
  white-space: nowrap;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  pointer-events: none;
  background: var(--dark-gray);
  outline: none;
}
@media (hover: none) {
  .tooltip {
    display: none;
  }
}
.button {
  width: 40px;
  height: 40px;
  background: var(--black);
  color: var(--white);
  transition: background var(--fast), color var(--fast);
}
.button:hover {
  background: var(--dark-gray);
}
.thin_button {
  width: 25px;
  height: 40px;
}

.text_box {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.text_box > * {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  color: var(--white);
  padding: 0;
  font-size: 1em;
  text-align: center;
  border: none;
  background: none;
  outline: none;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  transition: background var(--fast);
}
.text_box > div:hover {
  background: var(--dark-gray);
  cursor: cursor;
}
.title_box {
  width: 200px;
  height: 40px;
}
@media (max-width: 600px) {
  .title_box {
    width: 100%;
    order: -1;
  }
}
#top_panel {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  position: fixed;
  background: var(--black);
  left: 0;
  right: 0;
  top: 0;
  border-bottom: solid 2px var(--dark-gray);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
body[data-fullscreen="true"] #top_panel {
  visibility: hidden;
}
.undo_redo_menu_item {
  width: 100%;
  text-align: left;
  white-space: nowrap;
}
.side_panel_row {
  display: flex;
  justify-content: center;
  align-items: stretch;
  height: 40px;
}
.side_panel_row > * {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
}
.side_panel_row > *:nth-child(1) {
  width: 40px;
}
.side_panel_row > *:nth-child(2) {
  justify-content: flex-start;
  width: 120px;
  padding-left: 5px;
}
.side_panel_row > *:nth-child(3) {
  width: 80px;
}
.side_panel_row > *:nth-child(1) > * {
  color: var(--light-gray);
  height: 50%;
}

.side_panel_heading {
  box-sizing: border-box;
  width: 100%;
  padding: 10px 0 5px 10px;
  color: var(--light-gray);
  text-transform: uppercase;
  letter-spacing: 1px;
  text-align: left;
}

.check_box {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  height: 100%;
}
.check_box > button {
  width: 100%;
  height: 100%;
  padding: 0;
  font-size: 1em;
  text-align: center;
  border: none;
  background: none;
  outline: none;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  transition: background var(--fast);
}
.check_box > button:hover {
  background: var(--dark-gray);
}
.number_box {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  height: 100%;
}
.number_box > * {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  color: var(--white);
  padding: 0;
  font-size: 1em;
  text-align: center;
  border: none;
  background: none;
  outline: none;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  transition: background var(--fast);
}
.number_box > div:hover {
  background: var(--dark-gray);
  cursor: ns-resize;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type='number'] {
  -moz-appearance: textfield;
}

.color_picker {
  text-align: center;
}
.color_picker > *:not(:last-child) {
  margin-bottom: 10px;
}
.color_picker_frame {
  position: relative;
}
.color_picker_frame > * {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.color_picker_checkers {
  background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAKCAYAAACNMs+9AAAACXBIWXMAAA7EAAAOxAGVKw4bAAAALElEQVQYlWM8cODAfwY0YG9vjy7EwIQhggMMoELG////Y3jm4MGDdLCa+goBHw8KTtNafsMAAAAASUVORK5CYII=);
}
.color_picker_circle > * {
  border-radius: 100%;
  box-shadow: inset 0 0 0 1px var(--white);
}
.color_picker_button {
  width: 25px;
  height: 25px;
}
.color_picker_palette,
.color_picker_hue,
.color_picker_alpha {
  width: 200px;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.color_picker_palette {
  height: 200px;
}
.color_picker_palette_luminance {
  background: linear-gradient(
    to right,
    rgb(255, 255, 255),
    rgba(255, 255, 255, 0)
  );
}
.color_picker_palette_saturation {
  background: linear-gradient(to top, rgb(0, 0, 0), rgba(0, 0, 0, 0));
}
.color_picker_hue,
.color_picker_alpha {
  height: 15px;
}
.color_picker_hue_gradient {
  background: linear-gradient(
    to right,
    rgb(255, 0, 0) 0%,
    rgb(255, 255, 0) 17%,
    rgb(0, 255, 0) 33%,
    rgb(0, 255, 255) 50%,
    rgb(0, 0, 255) 67%,
    rgb(255, 0, 255) 83%,
    rgb(255, 0, 0) 100%
  );
}
.color_picker_hex {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 120px;
  height: 40px;
  color: var(--white);
  padding: 0;
  font-size: 1em;
  
  text-align: center;
  border: none;
  background: none;
  outline: none;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  transition: background var(--fast);
}
div.color_picker_hex:hover {
  background: var(--dark-gray);
  cursor: pointer;
}
.color_picker_swatches {
  width: 200px;
}
.color_picker_swatch {
  display: inline-block;
  width: 18px;
  height: 18px;
  margin: 2px;
}
.color_picker_marker_circle {
  width: 10px;
  height: 10px;
  transform: translate(-50%, -50%);
  border-radius: 5px;
  box-shadow: 0 0 0 1.5px var(--white);
  pointer-events: none;
}
.color_picker_marker_sliver {
  width: 5px;
  height: 20px;
  transform: translate(-50%, -50%);
  border-radius: 2.5px;
  background: var(--white);
  pointer-events: none;
}

.choice_box {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  height: 100%;
}
.choice_box > button {
  width: 100%;
  height: 100%;
  color: var(--white);
  padding: 0;
  font-size: 1em;
  text-align: center;
  text-align-last: center;
  border: none;
  background: none;
  outline: none;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  transition: background var(--fast);
}
.choice_box > button:hover {
  background: var(--dark-gray);
}
#side_panel {
  position: fixed;
  left: 0;
  top: 80px;
  transition: transform var(--fast);
  z-index: 1;
}
#side_panel[data-open="false"] {
  transform: translateX(-100%);
}
#side_panel .side_panel_row {
  transition: transform var(--fast);
}
#side_panel[data-open="false"] .side_panel_row {
  visibility: hidden;
}
.side_panel_button {
  position: absolute;
  left: 100%;
  top: 50%;
  width: 25px;
  height: 80px;
  border: solid 2px var(--dark-gray);
  border-left: none;
  transform: translateY(-50%);
}
.side_panel_header {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  border-top: solid 2px var(--dark-gray);
  border-right: solid 2px var(--dark-gray);
}
.side_panel_tab {
  position: relative;
  border-bottom: solid 2px var(--dark-gray);
  width: calc(100% / 3);
}
.side_panel_tab:not(:last-child) {
  border-right: solid 2px var(--dark-gray);
}
.side_panel_tab[data-active="false"] {
  color: var(--gray);
}
.side_panel_tab[data-active="true"] {
  color: var(--white);
  border-bottom: none;
}
.side_panel_content {
  background: var(--black);
  min-width: 250px;
  min-height: 240px;
  max-height: calc(100vh - 80px - 80px - 40px - 2px);
  overflow-x: hidden;
  overflow-y: auto;
  border-right: solid 2px var(--dark-gray);
  border-bottom: solid 2px var(--dark-gray);
}
body[data-fullscreen="true"] #side_panel {
  visibility: hidden;
}
.side_panel_empty {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 240px;
  color: var(--light-gray);
}
.timecode {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 10px;
  width: 110px;
  height: 40px;
}
.timecode_suffix {
  margin-left: 2px;
  margin-right: 6px;
}
@media (max-width: 600px) {
  .timecode {
    width: calc(100% - 40px * 3);
    height: 40px;
  }
}

.rail_container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-grow: 1;
  padding: 0 10px;
}
.rail {
  background: var(--gray);
  position: relative;
  width: 100%;
  height: 15px;
}
.rail:hover {
  cursor: pointer;
}
.rail_marker {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 50%;
  background: var(--blue);
  pointer-events: none;
}
.keyframe_markers {
  width: 100%;
  height: 15px;
}

#bottom_panel {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  position: fixed;
  background: var(--black);
  left: 0;
  right: 0;
  bottom: 0;
  border-top: solid 2px var(--dark-gray);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
body[data-fullscreen="true"] #bottom_panel {
  visibility: hidden;
}

* {
  font-family: "Poppins", sans-serif;

  --black: #202020;
  --dark-gray: #404040;
  --gray: #606060;
  --light-gray: #a0a0a0;
  --white: #ffffff;
  --blue: #42a5f5;
  --yellow: #ffeb3b;

  --slow: 1s ease;
  --fast: 0.25s ease;
}
html,
body {
  margin: 0;
  padding: 0;
  background: var(--black);
  color: var(--white);
}
#root {
  width: 100%;
  height: 100%;
}
button {
  border: 0;
  margin: 0;
  padding: 0;
  font-size: 1em;
  background: none;
  text-decoration: none;
  border: none;
  cursor: pointer;
}
input {
  font-size: 1em;
  background: none;
  border: none;
  color: var(--white);
  text-align: center;
}
*:focus {
  outline: none;
  box-shadow: inset 0 0 0 1px var(--blue);
}
svg {
  width: 20px;
  height: 20px;
  flex-shrink: 0;
  flex-grow: 0;
}
.small {
  font-size: 0.75em;
}
.light {
  color: var(--light-gray);
}
.flex_grow {
  flex-grow: 1;
}
*[data-color="gray"] {
  color: var(--gray);
}
*[data-color="white"] {
  color: var(--white);
}
*[data-color="blue"] {
  color: var(--blue);
}
*[data-color="yellow"] {
  color: var(--yellow);
}

